import { insertElement } from './insert-element';
export function insertImage(promptMsg, editor) {
    var url = window.prompt(promptMsg);
    if (url) {
        var img = document.createElement("img");
        img.src = url;
        img.tabIndex = 1;
        img.onblur = editor.onBlur;
        insertElement(img);
        editor.triggerOnChange();
    }
}
